import './scss/new.scss';

window.addEventListener('DOMContentLoaded', () => {
    let result = document.querySelector('.result'),
        pickingItem = document.querySelectorAll('.picking-item'),
        childRoomBtn = document.querySelector('.child-room'),
        roomList = document.querySelector('.room-list'),
        roomListItem = document.querySelectorAll('.room-list__item'),
        subListItem = document.querySelectorAll('.subItem__list-item'),
        reloadBtn = document.querySelector('.reload');
    
    childRoomBtn.addEventListener('click',(e)=>{
        e.target.classList.toggle('button__active');
        
        roomList.style.display === '' ? roomList.style.display = 'block' : roomList.style.display = "";
    });

    reloadBtn.addEventListener('click', ()=>{
        location.reload();
    });

    roomListItem.forEach((item, index)=>{
        item.addEventListener('click',()=>{
            for(let i = 0; i<roomListItem.length;i++){
                if(roomListItem[i].children[0].classList.contains('subItem__active')){
                    roomListItem[i].children[0].classList.remove('subItem__active');
                }
            }

            item.children[0].classList.add('subItem__active');
        });
    });
    
    subListItem.forEach(item=>{
        item.addEventListener('click', ()=>{
            let parentChildren = item.parentNode.children;
           
            for(let i = 0; i < parentChildren.length;i++){
                if(parentChildren[i].children[0]){
                    parentChildren[i].children[0].classList.contains('subItem__active') ? parentChildren[i].children[0].classList.remove('subItem__active') : null;
                }
                
            }
            if(item.children[0]){
                item.children[0].classList.add('subItem__active');
            }
            
        });
    });

    pickingItem.forEach(el=>{
        el.addEventListener('click', ()=>{
            if(result.innerHTML===''){
                result.innerText = `Вы выбрали:${el.innerText}`;
            } else {
                result.innerText += ` ${el.innerText}`;
            }
        });
    });
});